<template>
  <div>
    <!-- Header with Search Bar -->
    <header class="search-bar">
      <button @click="onSearch" class="search-btn">üîç</button>
      <input type="text" placeholder="Search..." v-model="searchQuery" />
      <button @click="onRefresh">‚ü≥</button>
    </header>

    <!-- Form to Display or Enter Data -->
    <div class="data-display">
      <!-- Invoice Name -->
      <div class="form-field">
        <label for="invoice-name">Invoice No:</label>
        <input type="text" id="invoice-name" v-model="formData.name" readonly />
      </div>

      <!-- Wrap the v-if around the parent element of the v-for loop -->
      <div v-if="formData.items.length > 0">
        <!-- Loop through Items -->
        <div v-for="(item, index) in formData.items" :key="index" class="item-form">
          <div class="form-field">
            <label :for="'item-code-' + index">Item Code:</label>
            <input type="text" :id="'item-code-' + index" v-model="item.name" />
          </div>
          <div class="form-field">
            <label :for="'item-name-' + index">Item Name:</label>
            <input type="text" :id="'item-name-' + index" v-model="item.item_name" />
          </div>
          <div class="form-field">
            <label :for="'description-' + index">Description:</label>
            <textarea :id="'description-' + index" v-model="item.description"></textarea>
          </div>
          <div class="form-field">
            <label :for="'brand-' + index">Brand:</label>
            <input type="text" :id="'brand-' + index" v-model="item.brand" />
          </div>
          <div class="form-field">
            <label :for="'mrp-' + index">MRP:</label>
            <input type="text" :id="'mrp-' + index" v-model="item.rate" />
          </div>
          <div class="form-field flex">
            <div>
              <label :for="'received-qty-' + index">Received QTY:</label>
              <input type="number" :id="'received-qty-' + index" v-model="item.received_qty" />
            </div>
            <div>
              <label :for="'accepted-qty-' + index">Accepted QTY:</label>
              <input type="number" :id="'accepted-qty-' + index" v-model="item.qty" />
            </div>
          </div>
          <div class="form-field">
            <label :for="'qc-pass-' + index">QC Pass:</label>
            <input type="checkbox" :id="'qc-pass-' + index" v-model="item.qc_pass" />
          </div>
        </div>
      </div>
    </div>

    <!-- Action Buttons -->
    <div class="action-buttons">
      <button @click="onSaveData" class="action-btn save-btn">Save Data</button>
      <button @click="onPrintBarcode" class="action-btn print-btn">Print Barcode</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      searchQuery: "",
      formData: {
        name: "",
        items: [
          {
            name: "",
            item_name: "",
            description: "",
            brand: "",
            rate: "",
            received_qty: "",
            qty: "",
            qc_pass: false,
          },
        ],
      },
    };
  },
  methods: {
    async onSearch() {
      try {
        if (this.searchQuery.trim()) {
          const response = await axios.get(
            `https://yash.tranqwality.com/api/resource/Purchase Invoice/${this.searchQuery}`,
            {
              headers: {
                Authorization: "token b73c5b96959aa53:1d8250a9d955d9b", // Replace with your API key and secret
              },
            }
          );

          const fetched = response.data.data;

          this.formData.name = fetched.name || "";
          this.formData.items = fetched.items || [];
        } else {
          console.log("Please enter a valid ID to search.");
        }
      } catch (error) {
        console.error("Error fetching data from Frappe:", error);
        alert("Failed to fetch data. Please try again.");
      }
    },
    async onSaveData() {
      try {
        if (!this.formData.name) {
          console.error("No Purchase Invoice to update.");
          alert("No Purchase Invoice to update.");
          return;
        }

        const response = await axios.put(
          `https://yash.tranqwality.com/api/resource/Purchase Invoice/${this.formData.name}`,
          { items: this.formData.items },
          {
            headers: {
              Authorization: "token b73c5b96959aa53:1d8250a9d955d9b", // Replace with your API key and secret
            },
          }
        );

        console.log("Data saved successfully:", response.data);
        alert("Data has been saved successfully.");
      } catch (error) {
        console.error("Error saving data to Frappe:", error);
        alert("Failed to save data. Please try again.");
      }
    },
    onPrintBarcode() {
      alert("Print Barcode functionality is not yet implemented.");
      // You can add your barcode generation/printing logic here.
    },
    onRefresh() {
      this.formData = {
        name: "",
        items: [
          {
            name: "",
            item_name: "",
            description: "",
            brand: "",
            rate: "",
            received_qty: "",
            qty: "",
            qc_pass: false,
          },
        ],
      };
      this.searchQuery = "";
    },
  },
};
</script>

<style scoped>
.search-bar {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.search-bar input {
  width: 85%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.search-bar button {
  padding: 8px 12px;
  background: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.data-display {
  max-width: 90%;
  background: white;
  padding: 20px;
  justify-content: center;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.form-field {
  margin-bottom: 15px;
  padding-right: 15px;
}

.form-field label {
  padding-right: 22px;
  font-size: 14px;
  color: #333;
}

.form-field input,
.form-field textarea {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  color: #555;
}

.item-form {
  margin-top: 20px;
}

.flex {
  display: flex;
  justify-content: space-between;
}

.action-buttons {
  text-align: center;
  margin-top: 20px;
  max-width: 90%;
  background-color: white;
  padding: 20px;
  justify-content: center;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.action-btn {
  height: 35px;
  width: 150px;
  background-color: green;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-bottom: 10px;
}

.print-btn {
  width: 100%;
}
</style>
